import http from '../axios';
import qs from 'query-string';
import _cloneDeep from 'lodash/cloneDeep';
import _get from 'lodash/get';
import _omit from 'lodash/omit';
import * as fileImportExportService from './fileImportExportService';

const endpoint = '/service-mapping/lora-generic-bacnet-mapping';

function mapDataToView(serviceMappings) {
  return serviceMappings.map(serviceMapping => {
    let serviceMappingO = {};
    if (serviceMapping.generic_point_uuid) {
      serviceMappingO = {
        genericPoint: {
          id: serviceMapping.generic_point_uuid,
          name: serviceMapping.generic_point_name,
        },
      };
    }

    if (serviceMapping.bacnet_point_uuid) {
      serviceMappingO = {
        ...serviceMappingO,
        bacnetPoint: {
          id: serviceMapping.bacnet_point_uuid,
          name: serviceMapping.bacnet_point_name,
        },
      };
    }
    return {
      ...serviceMappingO,
      uuid: serviceMapping.uuid,
      loraPoint: { id: serviceMapping.lora_point_uuid, name: serviceMapping.lora_point_name },
    };
  });
}

export function mapDataToRequest(serviceMapping) {
  let serviceMappingO = {};
  if (serviceMapping.bacnetPoint && serviceMapping.bacnetPoint.id) {
    serviceMappingO = {
      bacnet_point_uuid: serviceMapping.bacnetPoint.id,
      bacnet_point_name: serviceMapping.bacnetPoint.name,
    };
  }
  if (serviceMapping.genericPoint && serviceMapping.genericPoint.id) {
    serviceMappingO = {
      ...serviceMappingO,
      generic_point_uuid: serviceMapping.genericPoint.id,
      generic_point_name: serviceMapping.genericPoint.name,
    };
  }
  if (serviceMapping.uuid) {
    serviceMappingO = {
      ...serviceMappingO,
      uuid: serviceMapping.uuid,
    };
  }
  return {
    ...serviceMappingO,
    lora_point_uuid: serviceMapping.loraPoint.id || serviceMapping.loraPoint.uuid,
    lora_point_name: serviceMapping.loraPoint.name,
  };
}

export function appendNetworkDetails(serviceMapping, loraPointMap, bacnetPointsMap, genericPointsMap) {
  let networkDetails = {};
  networkDetails.lora_point = { ...loraPointMap[serviceMapping.lora_point_uuid] };

  if (serviceMapping.bacnet_point_name) {
    networkDetails.bacnet_point = { ...bacnetPointsMap[serviceMapping.bacnet_point_name] };
  }
  if (serviceMapping.generic_point_name) {
    networkDetails.generic_point = { ...genericPointsMap[serviceMapping.generic_point_name] };
  }
  networkDetails = _cloneDeep(networkDetails);
  if (networkDetails.lora_point && networkDetails.lora_point.id) {
    delete networkDetails.lora_point.id;
    delete networkDetails.lora_point.uuid;
  }

  if (networkDetails.generic_point && networkDetails.generic_point.id) {
    delete networkDetails.generic_point.id;
    delete networkDetails.generic_point.uuid;
    delete networkDetails.generic_point.device.uuid;
    delete networkDetails.generic_point.network.uuid;
    delete networkDetails.generic_point.point.uuid;
  }

  if (networkDetails.bacnet_point && networkDetails.bacnet_point.id) {
    delete networkDetails.bacnet_point.id;
    delete networkDetails.bacnet_point.uuid;
  }

  return networkDetails;
}

export const prepareFileExport = fileImportExportService.prepareFileExport;

export const prepareFileImport = fileImportExportService.prepareFileImport;

export const fetch = () => {
  return http.get(`${endpoint}`).then(mapDataToView);
};

export const createServiceMapping = (serviceMapping = {}) => {
  return http.post(`${endpoint}`, mapDataToRequest(serviceMapping));
};

export const updateServiceMapping = (serviceMapping = {}, uuid) => {
  const requestData = mapDataToRequest(serviceMapping);
  return http.patch(`${endpoint}/${uuid}`, requestData);
};

export const deleteServiceMapping = serviceMapping => {
  return http.delete(`${endpoint}/${serviceMapping.uuid}`);
};

export const deleteServiceMappings = serviceMappings => {
  return Promise.all(serviceMappings.map(serviceMapping => deleteServiceMapping(serviceMapping)));
};

export const bulkCreateServiceMapping = (serviceMappings, query = {}) => {
  return http.post(`${endpoint}/bulk-service-mapping?${qs.stringify(query)}`, serviceMappings);
};
