<template>
  <v-form v-model="valid" ref="mappingForm" :class="!isEditForm ? 'mb-4' : ''">
    <v-row dense>
      <v-col :cols="12">
        <v-autocomplete
          clearable
          v-if="isEditForm"
          return-object
          item-value="id"
          item-text="name"
          auto-select-first
          :rules="fieldValidations"
          :label="parentFieldTitle"
          v-model="selectedModbusPoint"
          :items="updatedParentPointsList"
        ></v-autocomplete>
        <v-autocomplete
          clearable
          return-object
          v-if="!isEditForm"
          item-value="id"
          item-text="name"
          auto-select-first
          :rules="[arrayRequiredValidation]"
          :label="modbusSelectionLabel"
          v-model="selectedModbusPoints"
          :items="updatedParentPointsList"
          @click:clear="onModbusPointsSelectionClear"
          multiple
        >
          <template v-slot:item="{ item, attrs }">
            <template v-if="item.isHeader">
              <v-list-item :class="`${item.isDevice ? 'pl-8' : ''}`" ripple @click="onDeviceSelectAllClick(item)">
                <v-icon v-if="item.isDevice" class="mr-2">
                  {{
                    selectedModbusDevices.indexOf(`${item.networkName}:::${item.name}`) > -1
                      ? 'mdi-close-box'
                      : 'mdi-checkbox-blank-outline'
                  }}
                </v-icon>
                <v-icon v-if="item.isNetwork" class="mr-2">
                  {{
                    selectedModbusNetworks.indexOf(`${item.name}`) > -1 ? 'mdi-close-box' : 'mdi-checkbox-blank-outline'
                  }}
                </v-icon>
                <v-list-item-title>
                  <span v-if="item.isDevice" class="caption">Select all modbus points of</span>
                  <span v-if="item.isNetwork" class="caption">Select all modbus devices of</span>
                  <strong>{{ item.name + ' ' }}</strong>
                </v-list-item-title>
              </v-list-item>
            </template>
            <template v-else>
              <v-list-item-content class="ml-12">
                <v-list-item-title>
                  <v-icon class="mr-2" v-if="!item.disabled">
                    {{ attrs['aria-selected'] == 'true' ? 'mdi-checkbox-marked' : 'mdi-checkbox-blank-outline' }}
                  </v-icon>
                  <span class="caption">{{ item.network.name }}</span>
                  <span class="caption">{{ item.device.name }}</span>
                  <strong>{{ item.point.name }}</strong>
                  <span v-if="item.disabled" class="already-mapped-label"> Already Mapped </span>
                </v-list-item-title>
              </v-list-item-content>
            </template>
          </template>

          <template v-slot:prepend-item>
            <v-list-item ripple @click="onModbusPointsSelectToggle">
              <v-icon class="mr-2">
                {{ isMultipleModbusPointsSelected ? 'mdi-close-box' : 'mdi-checkbox-blank-outline' }}
              </v-icon>
              <v-list-item-content>
                <v-list-item-title> Select all </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-divider class="mt-2"></v-divider>
          </template>
        </v-autocomplete>
      </v-col>
      <v-col cols="12">
        <v-card outlined>
          <v-card-title class="text-left">Bacnet Points</v-card-title>
          <v-card-text>
            <v-row dense>
              <v-col cols="12">
                <v-row dense>
                  <v-col cols="6">
                    <v-switch
                      @change="onAutoBacnetCreateToggle"
                      v-model="autoCreateBacnetPoints"
                      inset
                      class="pr-4"
                      label="Auto create new bacnet point(s) and mapping(s)"
                      color="primary"
                    ></v-switch>
                  </v-col>
                  <v-col cols="6">
                    <v-autocomplete
                      clearable
                      return-object
                      item-value="id"
                      item-text="name"
                      auto-select-first
                      :label="child1FieldTitle"
                      @change="checkValidation"
                      :rules="genericChildValidation"
                      v-model="selectedChild1Point"
                      :items="updatedChild1PointsList"
                      :disabled="!!selectedChild2Point || isMultipleModbusPointsSelected || autoCreateBacnetPoints"
                    ></v-autocomplete>
                  </v-col>
                </v-row>
              </v-col>
              <!-- Generated bacnet points start -->
              <v-col cols="12" v-if="!isMapMultipleModbusPoints && generatedBacnetPoints.length > 0">
                <v-item-group multiple class="text-left">
                  <v-subheader>Generated Bacnet Points</v-subheader>
                  <v-item v-for="item in generatedBacnetPoints" :key="item.name" class="ml-2 mb-2">
                    <v-chip active-class="green--text" :value="item">
                      <span>{{ item.name }}</span>
                      <v-btn rights icon color="white" class="pa-1" @click="onEditName('bacnetPoint', item)">
                        <v-icon right> mdi-pencil </v-icon>
                      </v-btn>
                    </v-chip>
                  </v-item>
                </v-item-group>
              </v-col>
              <!-- Generated generic points end -->
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12">
        <v-card outlined>
          <v-card-title class="text-left">Generic Points</v-card-title>
          <v-card-text>
            <v-row dense>
              <v-col cols="12" v-if="!isMapMultipleModbusPoints">
                <v-row dense>
                  <div class="existing-generic-mapping-wrapper">
                    <v-row dense>
                      <v-col cols="12">
                        <v-autocomplete
                          clearable
                          return-object
                          item-value="uuid"
                          :disabled="!(selectedModbusPoints && selectedModbusPoints.length > 0)"
                          item-text="name"
                          auto-select-first
                          :rules="genericChildValidation"
                          label="Select Existing Generic Network"
                          v-model="selectedGenericNetwork"
                          @change="onAutoCreateToggle"
                          :items="genericNetworks"
                        ></v-autocomplete>
                      </v-col>
                    </v-row>
                    <v-row dense>
                      <v-col cols="6">
                        <v-switch
                          @change="onAutoCreateToggle"
                          v-model="autoCreateDevicesAndPoints"
                          inset
                          class="pr-4"
                          label="Auto create new device(s), point(s) and mapping(s)"
                          color="primary"
                        ></v-switch>
                      </v-col>
                      <v-col cols="6">
                        <v-autocomplete
                          clearable
                          v-if="!autoCreateDevicesAndPoints"
                          return-object
                          item-value="uuid"
                          :disabled="!(selectedModbusPoints && selectedModbusPoints.length > 0)"
                          item-text="name"
                          auto-select-first
                          :rules="genericChildValidation"
                          label="Select Existing Generic Device"
                          v-model="selectedGenericDevice"
                          @change="onGenericDevicesChange"
                          :items="genericDevices"
                        ></v-autocomplete>
                      </v-col>
                    </v-row>
                  </div>
                  <v-autocomplete
                    clearable
                    v-if="isMapMultipleModbusPoints"
                    return-object
                    item-value="id"
                    item-text="name"
                    auto-select-first
                    @change="checkValidation"
                    :label="child2FieldTitle"
                    v-model="selectedChild2Point"
                    :rules="genericChildValidation"
                    :disabled="!!selectedChild1Point"
                    :items="updatedChild2PointsList"
                  ></v-autocomplete>
                  <!-- Generated generic points start -->
                  <v-col cols="12" v-if="!isMapMultipleModbusPoints && generatedGenericPoints.length > 0">
                    <v-item-group multiple class="text-left">
                      <v-subheader>Generated Generic Points</v-subheader>
                      <v-item v-for="item in generatedGenericPoints" :key="item.name" class="ml-2 mb-2">
                        <v-chip active-class="green--text" :value="item">
                          <span>{{ item.name }}</span>
                          <v-btn rights icon color="white" class="pa-1" @click="onEditName('genericPoint', item)">
                            <v-icon right> mdi-pencil </v-icon>
                          </v-btn>
                        </v-chip>
                      </v-item>
                    </v-item-group>
                  </v-col>
                  <!-- Generated generic points end -->
                </v-row>
              </v-col>
              <v-col cols="12">
                <v-autocomplete
                  clearable
                  v-if="isMapExistingGenericPointVisible"
                  return-object
                  item-value="id"
                  item-text="name"
                  auto-select-first
                  @change="checkValidation"
                  label="Existing Generic Point"
                  v-model="selectedChild2Point"
                  :rules="genericChildValidation"
                  :disabled="!!selectedChild1Point || autoCreateDevicesAndPoints"
                  :items="updatedChild2PointsList"
                ></v-autocomplete>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
      <v-col cols="12">
        <v-alert v-for="error in mappingErrors" :key="error.index" dense outlined type="warning">
          Mapping Error: <strong class="white--text">{{ error.modbus_point_name }}</strong> --
          <strong class="white--text">{{ error.child_point_name }}</strong> |
          <span>{{ error.message }}</span>
        </v-alert>
      </v-col>
      <v-col v-if="!isEditForm" cols="2" class="flex justify-center align-center">
        <rbx-button
          :loading="isCreatingMapping"
          :block="true"
          color="pink"
          :click="addMapping"
          title="Add"
        ></rbx-button>
      </v-col>
    </v-row>
    <v-card-actions v-if="isEditForm">
      <v-spacer></v-spacer>
      <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
      <v-btn color="blue darken-1" :loading="isCreatingMapping" text @click="addMapping"> Save </v-btn>
    </v-card-actions>
    <v-dialog v-model="isEditNameOpen" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Edit Name</span>
        </v-card-title>
        <v-card-text>
          <v-form v-model="editNameFormValid" ref="editNameForm">
            <v-text-field label="Point Name" required v-model="pointName" />
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="() => (isEditNameOpen = false)"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="onSaveName"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-form>
</template>
<script>
import _groupby from 'lodash/groupBy';
import _isEqual from 'lodash/isEqual';
import RbxButton from '../Common/RbxButton';
import validation from '../../common/validation.js';

export default {
  components: { RbxButton },
  props: [
    'serviceMappings',
    'isEditForm',
    'parentPoints',
    'child1Points',
    'child2Points',
    'serviceMapping',
    'parentMappingKey',
    'child1MappingKey',
    'child2MappingKey',
    'createServiceMapping',
    'createModbusBacnetGenericPointMapping',
    'parentFieldTitle',
    'child1FieldTitle',
    'child2FieldTitle',
    'onCancel',
    'genericNetworkDevices',
    'genericNetworks',
    'modbusNetworks',
  ],
  data() {
    return {
      uuid: null,
      valid: false,
      mappingErrors: [],
      modbusDeviceNames: [],
      modbusNetworkNames: [],
      modbusDeviceSelectionStates: {},
      modbusNetworkSelectionStates: {},
      autoCreateDevicesAndPoints: false,
      autoCreateBacnetPoints: false,
      selectedModbusDevices: [],
      selectedModbusNetworks: [],
      addNewNetworkToggle: false,
      addNewDeviceToggle: false,
      isMapMultipleModbusPoints: false,
      isMapExistingBacnetPoint: true,
      isCreatingMapping: false,
      selectedModbusPoint: null,
      selectedModbusPoints: [],
      selectedChild1Point: null,
      selectedChild2Point: null,
      selectedGenericNetwork: null,
      selectedGenericDevice: null,
      genericPointValidation: [validation.required],
      genericChildValidation: [validation.required],
      fieldValidations: [validation.required],
      generatedGenericPoints: [],
      generatedBacnetPoints: [],
      editNameFormValid: false,
      isEditNameOpen: false,
      pointName: '',
      editPointType: null,
      currentEditItem: {},
      arrayRequiredValidation: value => {
        return !!(Array.isArray(value) && value.length > 0) || 'Required';
      },
    };
  },
  watch: {
    selectedModbusPoints: function (newVal, oldVal) {
      this.onGenericDevicesChange();
      if (this.isMultipleModbusPointsSelected) {
        // Make bacnet point null as multiple mapping not supported yet for bacnet
        this.selectedChild1Point = null;

        this.isMapMultipleModbusPoints = false;
      }
    },
    modbusDeviceNames: function (newVal, oldVal) {
      if (!_isEqual(newVal, oldVal)) {
        this.modbusDeviceSelectionStates = this.modbusDeviceNames.reduce((acc, curr) => {
          acc[curr] = {
            name: curr,
            selected: false,
          };
          return acc;
        }, {});
      }
    },
    modbusNetworkNames: function (newVal, oldVal) {
      if (!_isEqual(newVal, oldVal)) {
        this.modbusNetworkSelectionStates = this.modbusNetworkNames.reduce((acc, curr) => {
          acc[curr] = {
            name: curr,
            selected: false,
          };
          return acc;
        }, {});
      }
    },
    isMapExistingGenericPointVisible: function (newVal, oldVal) {
      if (!_isEqual(newVal, oldVal)) {
        this.checkValidation();
      }
    },
  },
  mounted() {
    if (this.isEditForm && this.serviceMapping) {
      this.uuid = this.serviceMapping.uuid;
      this.isMapMultipleModbusPoints = true;
      this.selectedModbusPoint = this.serviceMapping[this.parentMappingKey];
      if (this.serviceMapping.hasOwnProperty(this.child1MappingKey)) {
        this.selectedChild1Point = this.serviceMapping[this.child1MappingKey];
      }
      if (this.serviceMapping.hasOwnProperty(this.child2MappingKey)) {
        this.selectedChild2Point = this.serviceMapping[this.child2MappingKey];
      }
      this.genericChildValidation = [];
    }
  },
  computed: {
    mappings() {
      return this.serviceMappings;
    },
    selectedParentMappings() {
      return this.serviceMappings.map(serviceMapping => serviceMapping[this.parentMappingKey]);
    },
    selectedChild2Mappings() {
      return this.serviceMappings.map(serviceMapping => serviceMapping[this.child2MappingKey]);
    },
    selectedChild1Mappings() {
      return this.serviceMappings.map(serviceMapping => serviceMapping[this.child1MappingKey]);
    },
    updatedParentPointsList() {
      this.selectedModbusDevices;
      this.selectedModbusNetworks;
      return this.updateParentListItemProperty(this.parentPoints, this.selectedParentMappings);
    },
    isMapExistingGenericPointVisible() {
      return this.selectedModbusPoints.length <= 1 && !this.isEditForm;
    },
    updatedChild2PointsList() {
      return this.updateLisItemProperty(this.child2Points, this.selectedChild2Mappings);
    },
    updatedChild1PointsList() {
      return this.updateLisItemProperty(this.child1Points, this.selectedChild1Mappings);
    },
    isAllModbusPointsSelected() {
      return this.selectedModbusPoints.length === this.updatedParentPointsList.length;
    },
    isMultipleModbusPointsSelected() {
      return this.selectedModbusPoints && this.selectedModbusPoints.length > 1;
    },
    genericDevices() {
      return (this.selectedGenericNetwork && this.selectedGenericNetwork.devices) || [];
    },
    modbusSelectionLabel() {
      if (this.selectedModbusPoints.length > 0) {
        return `${this.parentFieldTitle} | ${this.selectedModbusPoints.length} Modbus points selected`;
      }
      return this.parentFieldTitle;
    },
  },
  methods: {
    getServiceMappings() {
      if (this.autoCreateBacnetPoints) {
        return this.generatedBacnetPoints.map(serviceMapping => ({
          bacnet_point: serviceMapping.bacnetPoint,
          modbus_point: serviceMapping.modbusPoint,
        }));
      }
      if (this.autoCreateDevicesAndPoints) {
        return this.generatedGenericPoints.map(serviceMapping => ({
          generic_point: {
            network: serviceMapping.network,
            device: serviceMapping.device,
            point: serviceMapping.point,
          },
          modbus_point: serviceMapping.modbusPoint,
        }));
      }
    },
    addMapping() {
      const isValid = this.$refs.mappingForm.validate();
      if (!isValid) {
        return;
      }
      this.mappingErrors = [];
      if (
        !this.isEditForm &&
        !this.isMapMultipleModbusPoints &&
        (this.autoCreateDevicesAndPoints || this.autoCreateBacnetPoints)
      ) {
        this.isCreatingMapping = true;
        const serviceMappings = this.getServiceMappings();

        return this.createModbusBacnetGenericPointMapping(
          serviceMappings,
          this.autoCreateDevicesAndPoints,
          this.autoCreateBacnetPoints,
        )
          .then(data => {
            const hasError = data.every(obj => !obj.success);
            if (!hasError) {
              this.selectedModbusPoints = null;
              this.selectedModbusPoint = null;
              this.selectedChild2Point = null;
              this.selectedChild1Point = null;
              this.autoCreateDevicesAndPoints = false;
              this.autoCreateBacnetPoints = false;
              this.selectedModbusDevices = [];
              this.selectedModbusNetworks = [];
              this.$refs.mappingForm.reset();
            } else {
              this.extractDisplayError(data);
            }
          })
          .catch(err => {})
          .finally(() => {
            this.isCreatingMapping = false;
          });
      }
      if (!this.isEditForm && this.selectedModbusPoints.length == 1) {
        this.selectedModbusPoint = this.selectedModbusPoints[0].item;
      }
      this.isCreatingMapping = true;
      this.createServiceMapping(
        {
          [this.parentMappingKey]: this.selectedModbusPoint,
          [this.child1MappingKey]: this.selectedChild1Point,
          [this.child2MappingKey]: this.selectedChild2Point,
        },
        this.uuid,
      )
        .then(() => {
          if (this.isEditForm) {
            return this.onCancel();
          }
          this.selectedModbusPoints = null;
          this.selectedModbusPoint = null;
          this.selectedChild2Point = null;
          this.selectedChild1Point = null;
          this.$refs.mappingForm.reset();
        })
        .finally(() => {
          this.isCreatingMapping = false;
        });
    },
    extractDisplayError(data) {
      const errors = data
        .filter(data => !data.success)
        .map(item => {
          return {
            modbus_point_name: item.data && item.data.modbus_point_name,
            child_point_name: item.data && (item.data.generic_point_name || item.data.bacnet_point_name),
            message: item.message,
            index: item.rowIndex,
          };
        });
      this.mappingErrors = errors;
    },
    updateParentListItemProperty(list, selectedItems) {
      if (!selectedItems) {
        return list;
      }
      const selectedListItems = selectedItems.filter(item => item).map(item => item.id);
      const modbusDeviceNames = [];
      const modbusNetworkNames = [];
      const devices = list.reduce((acc, curr) => {
        if (selectedListItems.includes(curr.id)) {
          curr.disabled = true;
        } else {
          curr.disabled = false;
        }
        curr.networkGroup = curr.network.name;
        curr.deviceGroup = curr.device.name;

        acc.push(curr);
        return acc;
      }, []);
      const groupedNetworks = _groupby(devices, value => value.networkGroup);
      const groupedNetworkDevices = Object.keys(groupedNetworks).reduce((acc, curr) => {
        acc[curr] = _groupby(groupedNetworks[curr], val => val.deviceGroup);
        return acc;
      }, {});
      const newDeviceList = Object.keys(groupedNetworkDevices).reduce((acc, networkGroupName) => {
        let header = {
          text: networkGroupName,
          name: networkGroupName,
          itemName: `${networkGroupName}`,
          type: 'network',
          isNetwork: true,
          isHeader: true,
          isNonItem: true,
          selected: false,
        };
        acc.push(header);
        const pointList = Object.keys(groupedNetworkDevices[networkGroupName]).reduce((deviceAcc, deviceGroupName) => {
          let header = {
            text: deviceGroupName,
            type: 'device',
            isDevice: true,
            networkName: networkGroupName,
            name: deviceGroupName,
            itemName: `${networkGroupName}:::${deviceGroupName}`,
            isHeader: true,
            isNonItem: true,
            selected: false,
          };
          deviceAcc.push(header);
          deviceAcc.push(...groupedNetworkDevices[networkGroupName][deviceGroupName]);
          modbusDeviceNames.push(`${networkGroupName}:::${deviceGroupName}`);
          deviceAcc.push({ divider: true, isNonItem: true });
          return deviceAcc;
        }, []);
        acc.push(...pointList);
        // acc.push(...groupedDevices[networkGroupName]);
        modbusNetworkNames.push(`${networkGroupName}`);
        acc.push({ divider: true, isNonItem: true });
        return acc;
      }, []);
      this.modbusDeviceNames = [...modbusDeviceNames];
      this.modbusNetworkNames = [...modbusNetworkNames];
      return newDeviceList;
    },
    updateLisItemProperty(list, selectedItems) {
      if (!selectedItems) {
        return list;
      }
      const selectedListItems = selectedItems.filter(item => item).map(item => item.id);

      return list.reduce((acc, curr) => {
        if (selectedListItems.includes(curr.id)) {
          curr.disabled = true;
        } else {
          curr.disabled = false;
        }
        acc.push(curr);
        return acc;
      }, []);
    },
    checkValidation() {
      if (
        this.selectedChild1Point ||
        this.selectedChild2Point ||
        this.selectedGenericDevice ||
        this.autoCreateDevicesAndPoints ||
        this.autoCreateBacnetPoints
      ) {
        this.genericChildValidation.pop();
      }
      if (
        !this.selectedChild1Point &&
        !this.selectedChild2Point &&
        !this.selectedGenericDevice &&
        !this.autoCreateDevicesAndPoints &&
        !this.autoCreateBacnetPoints &&
        this.genericChildValidation.length < 1
      ) {
        this.genericChildValidation.push(validation.required);
      }
      this.$refs.mappingForm.resetValidation();
    },
    onModbusPointsSelectToggle() {
      this.$nextTick(() => {
        if (this.isMultipleModbusPointsSelected) {
          this.selectedModbusPoints = [];
        } else {
          this.selectedModbusPoints = this.updatedParentPointsList
            .slice()
            .filter(item => !item.disabled && !item.isHeader && !item.isNonItem);
        }
      });
    },
    onGenericDevicesChange() {
      if (
        Array.isArray(this.selectedModbusPoints) &&
        this.selectedModbusPoints.length > 0 &&
        this.selectedGenericDevice
      ) {
        this.generatedGenericPoints = this.selectedModbusPoints.reduce((acc, modbusPoint) => {
          const generatedModbusPoint = {};
          generatedModbusPoint.network = this.selectedGenericNetwork;
          generatedModbusPoint.device = this.selectedGenericDevice;
          generatedModbusPoint.point = modbusPoint.item;
          generatedModbusPoint.name = `${generatedModbusPoint.network.name}:${generatedModbusPoint.device.name}:${generatedModbusPoint.point.name}`;
          generatedModbusPoint.modbusPoint = modbusPoint;
          acc.push(generatedModbusPoint);
          return acc;
        }, []);
      } else {
        this.generatedGenericPoints = [];
      }
      this.checkValidation();
    },
    onMapExistingChange() {
      this.selectedChild2Point = null;
      if (this.genericChildValidation.length < 1) {
        this.genericChildValidation.push(validation.required);
      }
      this.generatedGenericPoints = [];
    },
    onDeviceSelectAllClick(item) {
      this.$nextTick(() => {
        const itemName = item.itemName;
        switch (item.type) {
          case 'network':
            this.onSeletAllModbusNetwork(itemName);
            break;
          case 'device':
            this.onSelectedAllModbusDevices(itemName);
            break;
        }
      });
    },
    onSeletAllModbusNetwork(itemName) {
      this.modbusNetworkSelectionStates[itemName].selected = !this.modbusNetworkSelectionStates[itemName].selected;
      if (this.modbusNetworkSelectionStates[itemName].selected) {
        this.selectedModbusNetworks.push(itemName);
      } else {
        this.selectedModbusNetworks = this.selectedModbusNetworks.filter(dName => dName !== itemName);
      }
      const selectedNetworkModbusDevices = this.modbusDeviceNames.reduce((acc, modbusDeviceName) => {
        if (modbusDeviceName.split(':::')[0] === itemName) {
          acc.push(modbusDeviceName);
          return acc;
        }
        return acc;
      }, []);
      for (let modbusDeviceName of selectedNetworkModbusDevices) {
        this.onSelectedAllModbusDevices(modbusDeviceName);
      }
    },
    onSelectedAllModbusDevices(itemName) {
      this.modbusDeviceSelectionStates[itemName].selected = !this.modbusDeviceSelectionStates[itemName].selected;
      if (this.modbusDeviceSelectionStates[itemName].selected) {
        this.selectedModbusDevices.push(itemName);
      } else {
        this.selectedModbusDevices = this.selectedModbusDevices.filter(dName => dName !== itemName);
      }
      this.selectedModbusPoints = this.updatedParentPointsList
        .filter(item => !item.isNonItem)
        .reduce((acc, listItem) => {
          if (
            !listItem.isHeader &&
            !listItem.disabled &&
            this.selectedModbusDevices.length > 0 &&
            this.selectedModbusDevices.findIndex(
              item => item.split(':::')[0] === listItem.network.name && item.split(':::')[1] === listItem.device.name,
            ) > -1
          ) {
            acc.push(listItem);
          }
          return acc;
        }, []);
    },
    getGenericDevice(networkUUID, deviceName) {
      return {
        driver: 'GENERIC',
        enable: false,
        fault: null,
        history_enable: false,
        name: deviceName,
        network_uuid: networkUUID,
      };
    },
    getBacnetPointName(networkName, deviceName, pointName) {
      return `${networkName}_${deviceName}_${pointName}`;
    },
    getBacnetPoint(modbusNetworkName, modbusDeviceName, modbusPointName) {
      return {
        object_type: 'analogOutput',
        object_name: this.getBacnetPointName(modbusNetworkName, modbusDeviceName, modbusPointName),
        address: null,
        relinquish_default: 1,
        priority_array_write: {
          _1: null,
          _2: null,
          _3: null,
          _4: null,
          _5: null,
          _6: null,
          _7: null,
          _8: null,
          _9: null,
          _10: null,
          _11: null,
          _12: null,
          _13: null,
          _14: null,
          _15: null,
          _16: null,
        },
        event_state: 'normal',
        units: 'noUnits',
        description: `modbus mapping ${modbusPointName}`,
        enable: true,
        use_next_available_address: true,
      };
    },
    onAutoCreateToggle() {
      if (this.autoCreateDevicesAndPoints) {
        this.autoCreateBacnetPoints = false;
        this.generatedBacnetPoints = [];
        this.selectedChild2Point = null;
      }
      if (this.autoCreateDevicesAndPoints && !!this.selectedGenericNetwork) {
        this.selectedGenericDevice = null;
        this.generatedGenericPoints = this.selectedModbusPoints.reduce((acc, modbusPoint) => {
          const generatedModbusPoint = {};
          generatedModbusPoint.network = this.selectedGenericNetwork;
          generatedModbusPoint.device = this.getGenericDevice(
            generatedModbusPoint.network.uuid,
            modbusPoint.device.name,
          );
          generatedModbusPoint.point = { ...modbusPoint.point };
          generatedModbusPoint.name = `${generatedModbusPoint.network.name}:${generatedModbusPoint.device.name}:${generatedModbusPoint.point.name}`;
          generatedModbusPoint.modbusPoint = { ...modbusPoint };
          acc.push(generatedModbusPoint);
          return acc;
        }, []);
      } else {
        this.generatedGenericPoints = [];
      }
      this.checkValidation();
    },
    onAutoBacnetCreateToggle() {
      if (this.autoCreateBacnetPoints) {
        this.autoCreateDevicesAndPoints = false;
        this.generatedGenericPoints = [];
        this.selectedChild1Point = null;
      }
      if (this.autoCreateBacnetPoints) {
        this.selectedChild1Point = null;
        this.generatedBacnetPoints = this.selectedModbusPoints.reduce((acc, modbusPoint) => {
          const generatedBacnetPoint = {};
          generatedBacnetPoint.modbusPoint = modbusPoint;
          generatedBacnetPoint.bacnetPoint = this.getBacnetPoint(
            modbusPoint.network.name,
            modbusPoint.device.name,
            modbusPoint.point.name,
          );
          generatedBacnetPoint.name = generatedBacnetPoint.bacnetPoint.object_name;
          acc.push(generatedBacnetPoint);
          return acc;
        }, []);
      } else {
        this.generatedBacnetPoints = [];
      }
      this.checkValidation();
    },
    onModbusPointsSelectionClear() {
      this.selectedModbusDevices = [];
      this.selectedModbusNetworks = [];
    },
    onEditName(pointType, item) {
      console.log(item);
      this.editPointType = pointType;
      switch (pointType) {
        case 'genericPoint':
          this.pointName = item.point.name;
          break;
        case 'bacnetPoint':
          console.log(item);
          this.pointName = item.bacnetPoint.object_name;
          break;
      }
      this.isEditNameOpen = true;
      this.currentEditItem = item;
    },
    onSaveName() {
      switch (this.editPointType) {
        case 'genericPoint':
          this.currentEditItem.point.name = this.pointName;
          this.currentEditItem.name = `${this.currentEditItem.network.name}:${this.currentEditItem.device.name}:${this.currentEditItem.point.name}`;
          break;
        case 'bacnetPoint':
          this.currentEditItem.bacnetPoint.object_name = this.pointName;
          this.currentEditItem.name = this.pointName;
          break;
      }
      this.isEditNameOpen = false;
    },
  },
};
</script>
